import React from 'react';
import {
  List,
  InputItem,
  NavBar,
  Icon,
  Grid
} from 'antd-mobile'
import QueueAnim from 'rc-queue-anim';
import {
  connect
} from 'react-redux'
import {
  getMsgList,
  sendMsg,
  recvMsg,
  readMsg
} from '../../reducers/chat'
import { getChatId } from '../../util.js';
// socket.on('recvmsg', function(data) {
//   console.log(data);
// })
@connect(
  state => state, {
    getMsgList,
    sendMsg,
    recvMsg,
    readMsg
  }
)


class Chat extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      text: '',
			showEmoji:false
    }
  }
  handleSubmit() {
    console.log('ke yi dian ji')
    // console.log(this.state)
    const from = this.props.user._id;
    const to = this.props.match.params.user;
    const msg = this.state.text
    this.props.sendMsg({
      from,
      to,
      msg
    })
    // socket.emit('sendmsg', {
    //   text: this.state.text
    // })
    this.setState({
			text:'',
			showEmoji:false
		})
  }
  //emoji bug
  fixCarousel(){
		setTimeout(function(){
			window.dispatchEvent(new Event('resize'))
		},0)
	}
  componentDidMount() {
    //
    //  dashboard  , getMsgList  ,dashboard, 
    //bug,chat dashboardchatrecvMsg
    if(!this.props.chat.chatmsg.length){
      //
      this.props.getMsgList()
      //
      this.props.recvMsg()
    }


  }
  componentWillUnmount(){
    // to id
    const to=this.props.match.params.user;
    this.props.readMsg(to)
    // componentWillUnmount() , componentDidMount() ,componentDidMount() ,,
  }
  render() {
    // console.log(this.props.chat.chatmsg)
    const emoji = '                                                                                                                         '
										.split(' ')
										.filter(v=>v)
										.map(v=>({text:v}))

    const userid = this.props.match.params.user;
    const Item = List.Item;
    const users = this.props.chat.users;
    //console.log(this.props.match.params.user)
    // console.log(this.props.chat.users[userid])
    if(!users[userid]){
      return null;
    }
    // useridid ,this,props.user._id id
    const chatId=getChatId(userid,this.props.user._id)
    //chatmsg ,
    const chatmsgs = this.props.chat.chatmsg.filter(v=>v.chatid===chatId);
    return (
      <div id="chat-page">
        <NavBar mode="dark" 
        className="chat-NavBar"
        icon={<Icon type="left" />}
        onLeftClick={() => this.props.history.goBack()}
        >{users[userid].name}</NavBar>
        <div style={{marginTop:45,marginBottom:45}}>
        <QueueAnim delay={100} type='bottom'>
          {chatmsgs.map(v=>{
            // console.log(users)
            const avatar = require(`../img/${users[v.from].avatar}.png`)
            // return <p>ok</p>
            return v.from===userid?(
              <List key={v._id}>
                <Item thumb={avatar}>{v.content}</Item>
              </List>
            ):(
              <List key={v._id}>
                 <Item extra={<img alt="emoji" src={avatar}/>} className="chat-me">{v.content}</Item>
              </List>
            )
           
          })}
          </QueueAnim>
        </div>
        <div className="stick-footer">
          <List>
            <InputItem
              focus='true'
              value={this.state.text}
              onChange={v=>{
                this.setState({text:v})
              }}
              extra={
                <div>
                <span
                style={{marginRight:15}}
                onClick={()=>{
                  this.setState({
                    showEmoji:!this.state.showEmoji
                  })
                  this.fixCarousel()
                }}
              ></span>
              <span onClick={()=>this.handleSubmit()}></span>
              </div>
                }
            ></InputItem>
          </List>
          {this.state.showEmoji?<Grid 
						data={emoji}
						columnNum={9}
						carouselMaxRow={4}
						isCarousel={true}
						onClick={el=>{
							this.setState({
								text:this.state.text+el.text
							})
							
						}}
					/>:null}
        </div>
      </div>
    )
  }
}
export default Chat;


